.articleholder{
    width: 100%;
    position: absolute;
    bottom: 0;
    background: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.3)) );
    background: -webkit-linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) );
    background: -o-linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) );
    background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3) );
    padding: 10px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
}

@media screen and (max-width: 600px) {
    .articleholder {
    display: none;
    }
}
.article{
    width: 20vw;
}

.articletext{
    height: 70px;
    color: #ffffff;
    overflow: hidden;    
}

.article + .article{
    padding-left: 15px;
    border-left: 1px solid #ffffff;
}

.knowmore{
    display: inline;
    color: #4A90E2;
    text-decoration: none;
}



